import React, { useState, useEffect } from 'react';
import {
    View,
    Panel,
    Group,
    PanelHeader,
    Placeholder,
} from '@vkontakte/vkui';

import { Icon28ServicesOutline, Icon28NewsfeedOutline } from "@vkontakte/icons"

const ManageCerts = ({id, setStory}) => {
    const [clickCount, setClickCount] = useState(0);

    const openOtherPage = () =>{
            if(clickCount == 3){
                setClickCount(0);
                setStory({currentTarget:{dataset:{story:"other"}}});
            }
            else {
                setClickCount(clickCount + 1)
            }
    }

    return (
            <Panel id={id}>
                <PanelHeader data-story="other" onClick={openOtherPage}>Управление сертификатами</PanelHeader>
                <Group style={{ height: '1000px' }}>
                    <Placeholder icon={<Icon28NewsfeedOutline width={56} height={56} />} />
                </Group>
            </Panel>
       
    )
}

export default ManageCerts;