import React, { useState, useEffect } from 'react';
import {
    View,
    Panel,
    Group,
    PanelHeader,
    Placeholder,
    Title,
    FormLayout,
    FormItem,
    Input,
    Button,
    Header,
    Textarea,
} from '@vkontakte/vkui';

import { Icon28ServicesOutline, Icon28NewsfeedOutline, Icon28BugOutline, } from "@vkontakte/icons"

const ConfigPage = ({ id }) => {

    const [config, setConfig] = useState(window.APP.config);


    useEffect(() => {

        console.log(config);

    })

    function saveConfig() {
        window.APP.config = config;
    }

    return (
        <Panel id={id}>
            <PanelHeader>Настройки</PanelHeader>
            
                    <Group>
                        <Header>Config</Header>
                        <FormLayout>
                            {Object.entries(config).map((el, num) =>
                            (<FormItem top={el[0]} key={num}>
                                <Input onChange={(e)=>{console.log(config);setConfig({...config, [el[0]]: e.target.value })}} value={el[1]} />
                            </FormItem>)
                            )}
                        </FormLayout>

                    </Group>
            
<Button size="l" onClick={saveConfig}>Сохранить</Button>
        </Panel>

    )
}

export default ConfigPage;