import React, { useState } from 'react';
import crypto from "crypto";
import {
    Panel,
    Group,
    PanelHeader,
    FormItem,
    Input,
    Separator,
    Header,
    Button,
    Select,
    Checkbox,
} from '@vkontakte/vkui';
import RandExp from "randexp";

const CreateCert = ({ id, usemodal }) => {
    const [selectedType, setSelectedType] = useState("VACCINE_CERT");
    const [titles, setTitles] = useState({ en: "COVID-19 vaccination certificate", ru: "Сертификат вакцинации от COVID-19" })
    const [certTypes, setCertTypes] = useState([{ name: "Вакцина", type: "VACCINE_CERT" }, { name: "Переболел", type: "ILLNESS_FACT" }, { name: "ПЦР тест", type: "COVID_TEST" },]);
    const [allFields, setallFields] = useState(true);
    const [hashValue, setHashValue] = useState("");
    const [pasportValue, setPasportValue] = useState("");
    const [certNumValue, setCertNumValue] = useState("");
    const [fioValue, setFioValue] = useState({ firsname: "", lastname: "", patronymic: "" });
    const [expireValue, setExpireValue] = useState("");
    const [brithValue, setBrithValue] = useState("");
    const [status, setStatus] = useState(1)

    function handleSubit(e) {
        usemodal(e, {
            preview: {
                type: selectedType,
                unrz: certNumValue,
                unrzFull: certNumValue,
                attrs: [
                    { type: "fio", ntitle: "ФИО", nentitle: "Full name", nenvalue: "K****** S***** IU******", value: fioValue.lastname.length > 0 && fioValue.firsname.replace(/(?!^)[\s\S]/g, '*') + " " + fioValue.lastname.replace(/(?!^)[\s\S]/g, '*') + " " + fioValue.patronymic.replace(/(?!^)[\s\S]/g, '*'), order: 1 },
                    { type: "birthDate", title: "Дата рождения", entitle: "Date of birth", envalue: brithValue, value: brithValue, order: 2 },
                    { type: "passport", title: "Паспорт", entitle: "National passport", envalue: pasportValue.length > 0 && pasportValue, value: pasportValue.length > 0 && pasportValue, order: 3 }],
                title: titles.ru,
                entitle: titles.en,
                status: status.toString(),
                expiredAt: expireValue,
                serviceUnavailable: false
            },
            data: {
                pasport: { pasportValue },
                fio: { preview: fioValue.lastname.length > 0 && fioValue.firsname.replace(/(?!^)[\s\S]/g, '*') + " " + fioValue.lastname.replace(/(?!^)[\s\S]/g, '*') + " " + fioValue.patronymic.replace(/(?!^)[\s\S]/g, '*'), fioValue },
                brith: brithValue,
                expire: expireValue,
                certNum: certNumValue,
                status: status,
                type: selectedType,
                hash: hashValue
            }

        })
    }

    const typeCrtChanger = (e) => {
        setSelectedType(e.target.value)
        switch (e.target.value) {
            case "VACCINE_CERT":
                setTitles({ en: "COVID-19 vaccination certificate", ru: "Сертификат вакцинации от COVID-19" })
                break;
            case "COVID_TEST":
                setTitles({ en: "COVID-19 test", ru: "Анализ на COVID-19" })
                break;
            case "ILLNESS_FACT":

                break;
            default:
                break;
        }
    }


    return (

        <Panel id={id}>
            <PanelHeader >Создание сертификата</PanelHeader>

            <Group description={"Символ * ставится автоматически"}>
                <Header>ФИО</Header>
                <FormItem top="Фамилия">
                    <Input type="text" id="firsname" value={fioValue.firsname} onChange={e => { setFioValue({ ...fioValue, firsname: e.target.value }) }} />
                </FormItem>
                <FormItem top="Имя">
                    <Input type="text" id="lastname" value={fioValue.lastname} onChange={e => { setFioValue({ ...fioValue, lastname: e.target.value }) }} />
                </FormItem>
                <FormItem top="Отчество">
                    <Input type="text" id="patronymic" value={fioValue.patronymic} onChange={e => { setFioValue({ ...fioValue, patronymic: e.target.value }) }} />
                </FormItem>
            </Group>
            <Group id="certData2">
                <Header>Данные сертификата</Header>
                <FormItem top="Дата рождения">
                    <Input type="date" id="brith" value={brithValue} onChange={e => { setBrithValue(e.target.value) }} />
                </FormItem>
                <FormItem top="Срок окончания">
                    <Input type="date" id="expire" value={expireValue} onChange={e => { setExpireValue(e.target.value) }} />
                </FormItem>
                <FormItem top="Паспорт">
                    <Input type="text" id="pasport" value={pasportValue} onChange={e => { setPasportValue(e.target.value) }} />
                    <Button mode="secondary" onClick={() => setPasportValue(new RandExp(/^([0-9]{4} [0-9]{6})?$/).gen())} style={{ "marginTop": "10px" }}>Сгенерировать</Button>
                </FormItem>
                <Separator />
                <FormItem top="Номер сретификата (9540 0000 **** ****)">
                    <Input type="text" id="numCert" value={certNumValue} onChange={e => { setCertNumValue(e.target.value) }} />
                    <Button mode="secondary" onClick={() => setCertNumValue(new RandExp(/^(95400000 [0-9]{4} [0-9]{4})?$/).gen())} style={{ "marginTop": "10px" }}>Сгенерировать</Button>
                </FormItem>
                <FormItem top="Hash">
                    <Input type="text" id="hash" value={hashValue} onChange={(e) => { setHashValue(e.target.value) }} />
                    <Button disabled={!(certNumValue.length > 0)} title="Генерируется на осонове номера сертивиката"
                        mode="secondary" onClick={() => { setHashValue(crypto.createHash("md5").update(certNumValue).digest("hex")) }} style={{ "marginTop": "10px" }}>Сгенерировать</Button>
                </FormItem>
                <Button modname="preview" disabled={!allFields} onClick={handleSubit} size="l" mode="commerce" stretched={true}>Создать</Button>
            </Group>
            <Group>
                <Header>Дополнительно</Header>
                <FormItem top="Тип сертификата">
                    <Select placeholder="Вакцина" onChange={typeCrtChanger} options={certTypes.map(el => ({ label: el.name, value: el.type }))} />
                </FormItem>

                <FormItem top="Статус">
                    <Checkbox defaultChecked onChange={() => { setStatus(1) }} >Действителен</Checkbox>
                    <Checkbox onChange={() => { setStatus("Отрицательный") }} >Отрицательный</Checkbox>
                    <Checkbox onChange={() => { setStatus("Положительный") }} >Положительный</Checkbox>
                    <Checkbox onChange={() => { setStatus(3) }} >Срок истёк</Checkbox>
                </FormItem>
            </Group>
        </Panel>

    )
}

export default CreateCert;