import React, { useState, useEffect } from 'react';
import '@vkontakte/vkui/dist/vkui.css';
import {
  Epic,
  Tabbar,
  View,
  TabbarItem,
  SplitCol,
  Panel,
  Cell,
  Group,
  SplitLayout,
  PanelHeader,
  ConfigProvider,
  AdaptivityProvider,
  AppRoot,
  ModalRoot,
  ModalCard,
  Button,
  PopoutWrapper,
  FormItem,
  Input,
  Header,
  FormLayout
} from '@vkontakte/vkui';

import { Icon24Settings, Icon24DocumentOutline } from "@vkontakte/icons"

import ConfigPage from "./Panels/ConfigPage"
import CreateCert from "./Panels/CreateCert";
import ManageCerts from './Panels/ManageCerts';

const App = () => {
  const [activeStory, setActiveStory] = useState('newCert');
  const [authData, setAuthData] = useState({login: "", password: ""})
  const [isAuthValid, setIsAuthValid] = useState(false)
  const [popout, setPopout] = useState(null)

  const [DataModal, setDataMmodal] = useState({});
	const [activeModal, setactiveModal] = useState(null);

  const onStoryChange = (e) => setActiveStory(e.currentTarget.dataset.story);
  const isDesktop = window.innerWidth >= 850;
  const hasHeader = true;


  
	const modalBack = () => {
		setactiveModal(null);
	};


	const setActiveModall = (e, data) => {

		let nameActiveModal = e.currentTarget.attributes.getNamedItem('modname').value

		setactiveModal(nameActiveModal)
		setDataMmodal(data)

	}


  const sendData = (e) =>{
    e.preventDefault();
    fetch(window.APP.config.apiUrl, {method: "POST", headers: {"Content-Type": "application/json"}, body: JSON.stringify({auth: {login: e.target[0].value, password:e.target[1].value}, data: DataModal})})
 
  }

  const auth = () =>{
    setPopout(<PopoutWrapper alignY="center" alignX="center">
      <Group>
        <Header>Авторизация</Header>
        <FormLayout onSubmit={sendData}>
        <FormItem status={isAuthValid} top="Логин">
          <Input type="text" onChange={e=>{setAuthData({...authData, login: e.target.value})}} />
        </FormItem>
        <FormItem status={isAuthValid} top="Пароль">
          <Input type="text"  onChange={e=>{setAuthData({...authData, password: e.target.value})}} />
        </FormItem>
        </FormLayout>  
        <Button style={{marginLeft: "10px"}} onClick={()=>{setPopout(null)}}>Закрыть</Button>
      </Group>
    </PopoutWrapper>)
  }

  const modal = (
    <ModalRoot viewWidth="600" activeModal={activeModal} onClose={modalBack}>
      <ModalCard id="preview">
 <iframe height="500" src={`./iframe.html?json=${JSON.stringify(DataModal.preview)}`} />
      <Button onClick={auth} mode="commerce" size="l" style={{marginTop: "7px"}} >Подтвердить</Button>
      </ModalCard>
    </ModalRoot>
  )

  

  return (

    <ConfigProvider platform="ios" scheme="space_gray">
      <AdaptivityProvider>
        <AppRoot>
          <SplitLayout
          popout={popout}
            modal={modal}
            header={hasHeader && <PanelHeader separator={false} />}
            style={{ justifyContent: "center" }}
          >
            {isDesktop && (
              <SplitCol fixed width={280} maxWidth={280}>
                <Panel>
                  {hasHeader && <PanelHeader />}
                  <Group>
                    <Cell
                      disabled={activeStory === 'newCert'}
                      style={activeStory === 'newCert' ? {
                        backgroundColor: "var(--button_secondary_background)",
                        borderRadius: 8
                      } : {}}
                      data-story="newCert"
                      onClick={onStoryChange}
                      before={<Icon24DocumentOutline/>}
                    >
                      Создание сертификата
                    </Cell>
                    <Cell
                      disabled={activeStory === 'mangeCerts'}
                      style={activeStory === 'mangeCerts' ? {
                        backgroundColor: "var(--button_secondary_background)",
                        borderRadius: 8
                      } : {}}
                      data-story="mangeCerts"
                      onClick={onStoryChange}
                      before={<Icon24Settings/>}
                    >
                      Управление созданными
                    </Cell>

                  </Group>
                </Panel>
              </SplitCol>
            )}

            <SplitCol
              animate={!isDesktop}
              spaced={isDesktop}
              width={isDesktop ? '560px' : '100%'}
              maxWidth={isDesktop ? '560px' : '100%'}
            >
              <Epic activeStory={activeStory} tabbar={!isDesktop &&
                <Tabbar>
                  <TabbarItem
                    onClick={onStoryChange}
                    selected={activeStory === 'newCert'}
                    data-story="newCert"
                    text="Создание"
                  ><Icon24DocumentOutline/></TabbarItem>
                  <TabbarItem
                    onClick={onStoryChange}
                    selected={activeStory === 'mangeCerts'}
                    data-story="mangeCerts"
                    text="Управление"
                  ><Icon24Settings/></TabbarItem>
                </Tabbar>
              }>
                <View id="newCert" activePanel="newCert">
                 <CreateCert auth={auth} usemodal={setActiveModall} id="newCert"/>
                </View>

                <View id="mangeCerts" activePanel="mangeCerts">
                <ManageCerts setStory={onStoryChange} id="mangeCerts" />
                </View>

                <View  data-story="oher" activePanel="other" id="other">
                  <ConfigPage id="other" />
                </View>

              </Epic>
            </SplitCol>
          </SplitLayout>
        </AppRoot>
      </AdaptivityProvider>
    </ConfigProvider>
  );
}
export default App;
